import pageTitle from '@/components/pageTitle'
export default {
  name: 'PageTitle',
  components: {
    pageTitle,
  },
  layout: 'header-footer',
  data() {
    return {
      content: '',
    }
  },
  async fetch() {
    const content = await this.$content('resources/software').fetch()
    this.content = content
  },
}
